"use client";

import type React from "react";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import {
  Play,
  CheckCircle2,
  Lock,
  Clock,
  Users,
  BookOpen,
  Trophy,
  Star,
  Sparkles,
} from "lucide-react";

type LessonStatus = "completed" | "in-progress" | "upcoming" | "locked";

interface Lesson {
  id: string;
  title: string;
  description: string;
  status: LessonStatus;
  duration?: string;
  participants?: number;
  color: string;
  icon: React.ReactNode;
}

const lessons: Lesson[] = [
  {
    id: "1",
    title: "Basic Greetings",
    description: "Learn essential sign language greetings and introductions.",
    status: "completed",
    duration: "15 min",
    participants: 234,
    color:
      "bg-emerald-100 dark:bg-emerald-950 border-emerald-300 dark:border-emerald-800",
    icon: <Star className="w-5 h-5 text-emerald-600" />,
  },
  {
    id: "2",
    title: "Common Phrases",
    description: "Master everyday phrases for effective communication.",
    status: "completed",
    duration: "20 min",
    participants: 189,
    color:
      "bg-emerald-100 dark:bg-emerald-950 border-emerald-300 dark:border-emerald-800",
    icon: <CheckCircle2 className="w-5 h-5 text-emerald-600" />,
  },
  {
    id: "3",
    title: "Numbers and Counting",
    description: "Learn to express numbers and quantities in sign language.",
    status: "in-progress",
    duration: "25 min",
    participants: 156,
    color:
      "bg-purple-100 dark:bg-purple-950 border-purple-300 dark:border-purple-800",
    icon: <Play className="w-5 h-5 text-purple-600" />,
  },
  {
    id: "4",
    title: "Family and Relations",
    description: "Understand signs for family members and relationships.",
    status: "upcoming",
    duration: "18 min",
    color:
      "bg-blue-50 dark:bg-blue-950/30 border-blue-200 dark:border-blue-900",
    icon: <Users className="w-5 h-5 text-blue-600" />,
  },
  {
    id: "5",
    title: "Food and Dining",
    description: "Express food preferences and dining experiences.",
    status: "upcoming",
    duration: "22 min",
    color:
      "bg-amber-50 dark:bg-amber-950/30 border-amber-200 dark:border-amber-900",
    icon: <BookOpen className="w-5 h-5 text-amber-600" />,
  },
  {
    id: "6",
    title: "Emotions and Feelings",
    description: "Communicate your emotions and understand others' feelings.",
    status: "locked",
    duration: "20 min",
    color: "bg-muted border-muted-foreground/20",
    icon: <Lock className="w-5 h-5 text-muted-foreground" />,
  },
  {
    id: "7",
    title: "Advanced Conversations",
    description: "Engage in complex discussions and express detailed thoughts.",
    status: "locked",
    duration: "30 min",
    color: "bg-muted border-muted-foreground/20",
    icon: <Lock className="w-5 h-5 text-muted-foreground" />,
  },
];

export default function MapPage() {
  const [selectedLesson, setSelectedLesson] = useState<string | null>(null);

  const completedCount = lessons.filter((l) => l.status === "completed").length;
  const totalCount = lessons.length;
  const progressPercentage = (completedCount / totalCount) * 100;

  const getStatusBadge = (status: LessonStatus) => {
    switch (status) {
      case "completed":
        return (
          <Badge className="bg-emerald-500 hover:bg-emerald-600 text-white gap-1">
            <CheckCircle2 className="w-3 h-3" />
            Completed
          </Badge>
        );
      case "in-progress":
        return (
          <Badge className="bg-purple-500 hover:bg-purple-600 text-white gap-1">
            <Clock className="w-3 h-3" />
            In Progress
          </Badge>
        );
      case "upcoming":
        return (
          <Badge variant="outline" className="gap-1">
            <Sparkles className="w-3 h-3" />
            Upcoming
          </Badge>
        );
      case "locked":
        return (
          <Badge variant="secondary" className="gap-1">
            <Lock className="w-3 h-3" />
            Locked
          </Badge>
        );
    }
  };

  return (
    <div className="space-y-6">
      {/* Learning Path */}
      <div className="relative max-w-6xl mx-auto">
        {/* Connecting Line - Centered */}
        <div className="absolute left-1/2 top-0 bottom-0 w-1 -ml-0.5 bg-gradient-to-b from-emerald-500 via-purple-500 to-muted-foreground/30" />

        <div className="space-y-8">
          {lessons.map((lesson, index) => {
            const isLeft = index % 2 === 0;

            return (
              <div key={lesson.id} className="relative">
                {/* Node Circle - Centered on timeline */}
                <div
                  className={`absolute left-1/2 top-6 -ml-4 w-8 h-8 rounded-full border-4 border-background flex items-center justify-center z-10 ${
                    lesson.status === "completed"
                      ? "bg-emerald-500"
                      : lesson.status === "in-progress"
                      ? "bg-purple-500"
                      : lesson.status === "upcoming"
                      ? "bg-primary"
                      : "bg-muted-foreground/30"
                  }`}
                >
                  {lesson.status === "completed" && (
                    <CheckCircle2 className="w-4 h-4 text-white" />
                  )}
                  {lesson.status === "in-progress" && (
                    <Play className="w-4 h-4 text-white" />
                  )}
                  {lesson.status === "locked" && (
                    <Lock className="w-3 h-3 text-white" />
                  )}
                </div>

                {/* Lesson Card - Alternating sides */}
                <div className={`grid grid-cols-2 gap-8 ${isLeft ? "" : ""}`}>
                  {/* Left side lesson */}
                  {isLeft && (
                    <>
                      <div className="pr-8">
                        <Card
                          className={`p-6 transition-all duration-300 cursor-pointer hover:shadow-lg ${
                            lesson.color
                          } ${
                            selectedLesson === lesson.id
                              ? "ring-2 ring-primary scale-[1.02]"
                              : ""
                          } ${lesson.status === "locked" ? "opacity-60" : ""}`}
                          onClick={() =>
                            lesson.status !== "locked" &&
                            setSelectedLesson(
                              lesson.id === selectedLesson ? null : lesson.id
                            )
                          }
                        >
                          <div className="flex items-start justify-between mb-3">
                            <div className="flex items-center gap-3">
                              <div className="p-2 rounded-lg bg-background/50">
                                {lesson.icon}
                              </div>
                              <div>
                                <h3 className="font-bold text-lg">
                                  {lesson.title}
                                </h3>
                                {getStatusBadge(lesson.status)}
                              </div>
                            </div>
                            {lesson.status !== "locked" && (
                              <Button
                                size="sm"
                                className={
                                  lesson.status === "completed"
                                    ? "bg-emerald-500 hover:bg-emerald-600"
                                    : lesson.status === "in-progress"
                                    ? "bg-purple-500 hover:bg-purple-600"
                                    : ""
                                }
                                disabled={lesson.status === "locked"}
                              >
                                {lesson.status === "completed"
                                  ? "Review"
                                  : lesson.status === "in-progress"
                                  ? "Continue"
                                  : "Start"}
                              </Button>
                            )}
                          </div>

                          <p className="text-sm text-muted-foreground mb-4">
                            {lesson.description}
                          </p>

                          <div className="flex items-center gap-4 text-sm text-muted-foreground">
                            {lesson.duration && (
                              <div className="flex items-center gap-1">
                                <Clock className="w-4 h-4" />
                                {lesson.duration}
                              </div>
                            )}
                            {lesson.participants && (
                              <div className="flex items-center gap-1">
                                <Users className="w-4 h-4" />
                                {lesson.participants} learners
                              </div>
                            )}
                          </div>

                          {selectedLesson === lesson.id &&
                            lesson.status !== "locked" && (
                              <div className="mt-4 pt-4 border-t border-border/50 space-y-2">
                                <h4 className="font-semibold text-sm">
                                  What you'll learn:
                                </h4>
                                <ul className="text-sm text-muted-foreground space-y-1 list-disc list-inside">
                                  <li>Core vocabulary and essential signs</li>
                                  <li>Proper hand shapes and movements</li>
                                  <li>Cultural context and usage tips</li>
                                  <li>Practice exercises with feedback</li>
                                </ul>
                              </div>
                            )}
                        </Card>
                      </div>
                      <div /> {/* Empty right side */}
                    </>
                  )}

                  {/* Right side lesson */}
                  {!isLeft && (
                    <>
                      <div /> {/* Empty left side */}
                      <div className="pl-8">
                        <Card
                          className={`p-6 transition-all duration-300 cursor-pointer hover:shadow-lg ${
                            lesson.color
                          } ${
                            selectedLesson === lesson.id
                              ? "ring-2 ring-primary scale-[1.02]"
                              : ""
                          } ${lesson.status === "locked" ? "opacity-60" : ""}`}
                          onClick={() =>
                            lesson.status !== "locked" &&
                            setSelectedLesson(
                              lesson.id === selectedLesson ? null : lesson.id
                            )
                          }
                        >
                          <div className="flex items-start justify-between mb-3">
                            <div className="flex items-center gap-3">
                              <div className="p-2 rounded-lg bg-background/50">
                                {lesson.icon}
                              </div>
                              <div>
                                <h3 className="font-bold text-lg">
                                  {lesson.title}
                                </h3>
                                {getStatusBadge(lesson.status)}
                              </div>
                            </div>
                            {lesson.status !== "locked" && (
                              <Button
                                size="sm"
                                className={
                                  lesson.status === "completed"
                                    ? "bg-emerald-500 hover:bg-emerald-600"
                                    : lesson.status === "in-progress"
                                    ? "bg-purple-500 hover:bg-purple-600"
                                    : ""
                                }
                                disabled={lesson.status === "locked"}
                              >
                                {lesson.status === "completed"
                                  ? "Review"
                                  : lesson.status === "in-progress"
                                  ? "Continue"
                                  : "Start"}
                              </Button>
                            )}
                          </div>

                          <p className="text-sm text-muted-foreground mb-4">
                            {lesson.description}
                          </p>

                          <div className="flex items-center gap-4 text-sm text-muted-foreground">
                            {lesson.duration && (
                              <div className="flex items-center gap-1">
                                <Clock className="w-4 h-4" />
                                {lesson.duration}
                              </div>
                            )}
                            {lesson.participants && (
                              <div className="flex items-center gap-1">
                                <Users className="w-4 h-4" />
                                {lesson.participants} learners
                              </div>
                            )}
                          </div>

                          {selectedLesson === lesson.id &&
                            lesson.status !== "locked" && (
                              <div className="mt-4 pt-4 border-t border-border/50 space-y-2">
                                <h4 className="font-semibold text-sm">
                                  What you'll learn:
                                </h4>
                                <ul className="text-sm text-muted-foreground space-y-1 list-disc list-inside">
                                  <li>Core vocabulary and essential signs</li>
                                  <li>Proper hand shapes and movements</li>
                                  <li>Cultural context and usage tips</li>
                                  <li>Practice exercises with feedback</li>
                                </ul>
                              </div>
                            )}
                        </Card>
                      </div>
                    </>
                  )}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
